<template>
  <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
    <IconWithName
      v-for="(item, index) in friendData"
      :key="index"
      :image="''"
      defaultImageType="user"
      :name="item.name"
      :active="item.live"
      :subLine="$t('myPage.join') + ' 4 ' + $t('myPage.month_ago')"
      :outLineBtnText="$t('myPage.remove')"
    />
    <div class="mt-3 text-center" v-if="friendData.length < friendTotal">
      <IgButton
        class="ig-small px-3 ig-font-small ig-add-border"
        buttonName="see_more"
        textColor="ig-white-text"
        @click="getFriends(friendPage + 1)"
      />
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import { mapState } from "vuex";
import axios from "axios";

export default defineComponent({
  data() {
    return {
      friendPage: 1,
      friendTotal: 0,
      friendData: [],
    };
  },
  components: {
    IconWithName,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    getSubLine(item) {
      var currentDate = new Date();
      var date = new Date(item.created);
      var months = 0;
      months = (currentDate.getFullYear() - date.getFullYear()) * 12;
      months -= date.getMonth();
      months += currentDate.getMonth();
      months = months <= 0 ? 0 : months;

      return (
        this.$t("userPage.join") +
        " " +
        months +
        " " +
        this.$t("userPage.month_ago")
      );
    },
    async getFriends(page) {
      var response = await axios.get(this.apiUrl + "/profile/friends", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.friendPage = page;
        this.friendTotal = response.data.count;
        if (page == 1) this.friendData = [];
        this.friendData = this.friendData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getFriends(1);
  },
});
</script>
