<template>
  <div class="grid grid-cols-1 px-3 md:px-0">
    <div class="md:flex mb-10 text-center md:text-left">
      <div style="width: 8rem" class="mx-auto md:mx-0">
        <!-- <img src="@/assets/images/user.png" class="w-full rounded-full" />/ -->
        <img :src="profile_picture" class="w-full rounded-full" />
      </div>
      <div class="md:ml-6 mt-6 md:my-auto">
        <button class="upload-btn">
          {{ $t("myPage.upload_profile_picture") }}
        </button>
      </div>
    </div>
    <div class="grid grid-cols-1 md:grid-cols-5 gap-10">
      <div class="md:col-span-2">
