          <lable>{{ $t("myPage.password") }}</lable>
          <input type="password" v-model="password" />
        </div>
        <div class="mt-6">
          <IgButton
            @click="showChangePasswordDialog = true"
            class="ig-full px-10"
            buttonName="change_password"
            textColor="ig-black-text"
            backgroundColor="ig-white"
          />
        </div>
      </div>
      <div class="md:col-span-2">
        <div class="grid grid-row-2 gap-1 mb-3">
          <lable>{{ $t("myPage.about") }}</lable>
          <textarea rows="6" v-model="about"></textarea>
        </div>
      </div>
    </div>
  </div>
  <Dialog
    :open="showChangePasswordDialog"
    @close="showChangePasswordDialog = !showChangePasswordDialog"
  >
    <div class="change-password-dialog">
      <div>
        <div class="flex mb-6">
          <p class="text-xl">
            <b>{{ $t("myPage.change_password") }}</b>
          </p>
          <a
            href="javascript:void(0)"
            class="my-auto ml-auto"
            @click="showChangePasswordDialog = !showChangePasswordDialog"
          >
            <svg
              width="1rem"
              height="1rem"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M15.6548 1.40965L14.5581 0.325903C14.2613 0.029129 13.771 0.029129 13.4613 0.325903L7.99032 5.79674L2.41626 0.222677C2.11935 -0.0742258 1.62903 -0.0742258 1.31948 0.222677L0.222708 1.31945C-0.0741953 1.61623 -0.0741953 2.10655 0.222708 2.41623L5.784 7.97752L0.325934 13.4614C0.0291595 13.7582 0.0291595 14.2485 0.325934 14.5582L1.42271 15.6549C1.71948 15.9517 2.2098 15.9517 2.51948 15.6549L7.99032 10.184L13.4613 15.6549C13.7581 15.9517 14.2484 15.9517 14.5581 15.6549L15.6548 14.5582C15.9516 14.2614 15.9516 13.7711 15.6548 13.4614L10.171 7.99042L15.6419 2.51958C15.9516 2.20965 15.9516 1.71932 15.6548 1.40965Z"
                fill="white"
              />
            </svg>
          </a>
        </div>
        <div class="grid grid-row-2 gap-1 mb-3">
          <lable>{{ $t("myPage.current_password") }}</lable>
          <input type="password" v-model="old_password" />
        </div>
        <div class="grid grid-row-2 gap-1 mb-3">
          <lable>{{ $t("myPage.new_password") }}</lable>
          <input type="password" v-model="new_password" />
        </div>
        <div class="grid grid-row-2 gap-1 mb-3">
          <lable>{{ $t("myPage.confirm_password") }}</lable>
          <input type="password" v-model="confirm_password" />
        </div>
        <div class="mt-6">
          <IgButton
            class="ig-full px-10"
            buttonName="save"
            :textColor="checkChangePassword ? 'ig-white-text' : 'ig-gray-text'"
            :backgroundColor="checkChangePassword ? 'ig-purple' : 'ig-gray'"
            @click="checkChangePassword ? changePassword() : ''"
          />
        </div>
      </div>
    </div>
  </Dialog>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import { Dialog } from "@headlessui/vue";
import axios from "axios";

import IgButton from "@/components/baseUI/IgButton.vue";
import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      showChangePasswordDialog: false,
      profile_picture: "",
      username: "Gaurav Moradiya",
      email_id: "sm.gaurav.moradiya@gmail.com",
      password: "123456",
      about:
        "Nemo enim ipsm voluptatem quia voluptas sit aspernatur aut emo enim ipsam voluptatem quia voluptas Nemo enim ipsm voluptatem quia voluptas sit aspernatur aut emo enim ipsam voluptatem quia voluptas",

      old_password: "",
      new_password: "",
      confirm_password: "",
    };
  },
  components: {
    IgButton,
    Dialog,
  },
  computed: {
    ...mapState(["apiUrl"]),
    checkChangePassword() {
      if (
        this.old_password != "" ||
        this.new_password != "" ||
        this.confirm_password != ""
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    async getProfile() {
      var response = await axios.get(this.apiUrl + "/profile", {
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response != null) {
        this.username = response.data.full_name;
        this.about = response.data.description;
        this.profile_picture = response.data.profile_picture;
      }
    },
    async changePassword() {
      var response = await axios.put(
        this.apiUrl + "/password",
        {
          current: this.old_password,
          new: this.new_password,
          confirm: this.confirm_password,
        },
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        }
      );
      if (response.data.code == 200) {
        this.showChangePasswordDialog = false;
      }
    },
  },
  created() {
    this.getProfile();
  },
});
</script>
