<template>
  <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
    <IconWithName
      v-for="(item, index) in teamData"
      :key="index"
      :image="item.team_icon"
      defaultImageType="team"
      :name="item.name"
      :subLine="item.team_members.length + ' ' + $t('myPage.members')"
      :outLineBtnText="$t('myPage.exit_team')"
    />
  </div>
  <div class="mt-3 text-center" v-if="teamData.length < teamTotal">
    <IgButton
      class="ig-small px-3 ig-font-small ig-add-border"
      buttonName="see_more"
      textColor="ig-white-text"
      @click="getTeam(teamPage + 1)"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";
import { mapState } from "vuex";
import axios from "axios";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  data() {
    return {
      teamPage: 1,
      teamTotal: 0,
      teamData: [],
    };
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  components: {
    IconWithName,
    IgButton,
  },
  methods: {
    async getTeam(page) {
      var response = await axios.get(this.apiUrl + "/profile/team/", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.teamPage = page;
        this.teamTotal = response.data.count;
        if (page == 1) this.teamData = [];
        this.teamData = this.teamData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTeam(1);
  },
});
</script>
