
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";
import { mapState } from "vuex";
import axios from "axios";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  data() {
    return {
      teamPage: 1,
      teamTotal: 0,
      teamData: [],
    };
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  components: {
    IconWithName,
    IgButton,
  },
  methods: {
    async getTeam(page) {
      var response = await axios.get(this.apiUrl + "/profile/team/", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.teamPage = page;
        this.teamTotal = response.data.count;
        if (page == 1) this.teamData = [];
        this.teamData = this.teamData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTeam(1);
  },
});
