<template>
  <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
    <IconWithName
      v-for="(data, index) in spacesData"
      :key="index"
      :image="data.thumb"
      defaultImageType="team"
      :name="data.name"
      :subLine="data.members_count + ' ' + $t('userPage.members')"
      :outLineBtnText="$t('myPage.exit_space')"
    />
    <div class="mt-3 text-center" v-if="spacesData.length < spacesTotal">
      <IgButton
        class="ig-small px-3 ig-font-small ig-add-border"
        buttonName="see_more"
        textColor="ig-white-text"
        @click="getSpace(spacesPage + 1)"
      />
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      spacesData: [],
      spacesPage: 1,
      spacesTotal: 0,
    };
  },
  components: {
    IconWithName,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getSpaces(page) {
      var response = await axios.get(this.apiUrl + "/profile/spaces", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.spacesPage = page;
        this.spacesTotal = response.data.count;
        if (page == 1) this.spacesData = [];
        this.spacesData = this.spacesData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getSpaces(1);
  },
});
</script>
